@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Inconsolata:wght@200;300;400;500;600;700;800;900&display=swap);
body{margin:0px;background-color:#BDE5E1;font-family:'Bebas Neue', cursive;font-family:'Inconsolata', monospace}

#home-container{min-height:100vh;min-width:100vw;position:relative;display:flex;justify-content:center;align-items:center}@-webkit-keyframes blink{0%{background-color:#F88767}15%{background-color:#366DB2}30%{background-color:#8A6CB7}58%{background-color:#C669A7}85%{background-color:#EC7089}100%{background-color:#006998}}@keyframes blink{0%{background-color:#F88767}15%{background-color:#366DB2}30%{background-color:#8A6CB7}58%{background-color:#C669A7}85%{background-color:#EC7089}100%{background-color:#006998}}#home-container .home-bg-circle{background-image:url(/static/media/bg.71ceb7c0.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;background-color:#F88767;background-blend-mode:screen;display:flex;justify-content:center;align-items:center;text-align:center;height:400px;width:400px;color:#006998;-webkit-clip-path:polygon(50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%);clip-path:polygon(50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%);animation:blink 5s infinite;-webkit-animation:blink 5s infinite}#home-container .home-social-media{position:absolute;bottom:20px;width:200px;display:flex;justify-content:space-around}#home-container .home-social-media .icon{width:25px;height:25px}#home-container .home-fullname{font-weight:600;font-size:2rem}#home-container .home-fullname .home-fullname-title{font-weight:500;font-size:1.5rem}#home-container .home-my-profile{position:absolute;right:20px;bottom:20px}

